*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
$bg : #f3ecea;

    body{
        background-color: $bg;
    }

.navbar{
    .container{
        .navbar-brand{
            font-size: 1.7rem;
        }
        .nav-items{
            ul{
                width: 100%;
                justify-content: center;
                li{
                    font-size: 1.3rem;
                }
            }
            form{
                input{
                    border-radius: 5rem;
                }
            }
        }
    }
}