* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  a {
    text-decoration: none;
    color: #000;
    &:hover {
      color: #000;
    }
  }
  
  $shadow: 0px 0px 4px gray;
  $theme: rgba(216, 216, 216, 0.24);
  $border: 10px;
  $border_2 :5px;
  

  .insta {
    width: 50px;
    aspect-ratio: 1/1;
    position: fixed;
    right: 1rem;
    bottom: 2rem;
    z-index: 100;
    img {
      width: 100%;
    }
  }
  
  .btn_1{
      width: fit-content;
      padding: .5rem 1rem;
      box-shadow: $shadow;
      border-radius: $border_2;
      cursor: pointer;
      // font-size: 1.2rem;
  
  }
  
  .home_cards{
      max-width: 1144px;
      margin: auto;
      padding: 1rem;
      .header{
          .name_explore{
              display: flex;
              justify-content: space-between;
              padding: 1rem;
              .name{
                  font-size: 1.5rem;
              }
          }
          .cards{
              display: flex;
              flex-wrap: wrap;
              justify-content: center;
              .card{
                  width: 200px;
                  height: 250px;
                  box-shadow: $shadow;
                  margin: 1rem;
              }
          }
      }
  }

  @media only screen and (max-width: 500px) {
    .home_cards{
      .header{
          .cards{
            .card{
                  width: 100%;
                  height: 100%;
                  margin: 1rem;
              }
          }
      }
  }
  }
  
  footer {
    background: #000;
    width: 100%;
    // height: 30rem;
    .footer_elements {
      width: 100%;
      display: flex;
      justify-content: space-evenly;
      flex-wrap: wrap;
      .links {
        min-height: fit-content;
        padding: 2rem;
        .heading {
          font-size: 1.3rem;
          color: #fff;
          font-weight: 600;
          position: relative;
          border-bottom: 2px solid rebeccapurple;
          width: fit-content;
          margin-bottom: 0.5rem;
        }
        a {
          color: #d3d3d3;
          &:hover {
            color: #fff;
          }
        }
      }
    }
    .lower_footer {
      width: 100%;
      height: 3rem;
      background: gray;
      display: grid;
      place-items: center;
      padding: 0 2rem;
      p {
        margin: 0;
      }
    }
  }
  
  @media only screen and (max-width: 800px) {
    footer {
      .footer_elements {
        .links {
          min-width: 300px;
          padding: 1rem;
        }
      }
    }
  }
  
  @media only screen and (max-width: 600px) {
    footer {
      .footer_elements {
        .links {
          min-width: 50%;
          padding: 0.5rem;
        }
      }
    }
  }
  @media only screen and (max-width: 450px) {
    footer {
      .footer_elements {
        display: grid;
        .links {
          min-width: 100%;
          padding: 0.5rem;
        }
      }
      .lower_footer {
        height: 2rem;
        p {
          font-size: 0.8rem;
        }
      }
    }
  }
  



