.card{
    // min-width:300px;
    // max-width: 300px;
    box-shadow: 0 0 10px rgb(0, 0, 0);
    border-radius: 10px;
    border: 0;
    height: fit-content;
}
.card img{
    width: 100%;
    position: relative;
    /* padding: 1rem; */
    border-radius: 10px;
}

 .card .img_navigation{
    /* width: calc(100% - 2rem); */
    width: 100%;
    height: 5rem;
    /* margin:0 1rem; */

    background-color: rgba(0, 0, 0, 0.5);
    border-radius:0 0 10px 10px;
    position: absolute;
    bottom: 0;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    color: #fff;   
}

.downloded_btn, .share_btn{
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    display: grid;
    place-items: center;
    background: rgba(0, 0, 0, 0.6);
    box-shadow: 0 0 2px gray;
}
