*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;

}
a{
    text-decoration: none;
    color: #000;
    &:hover{
        color: #000;
    }
}

$shadow : 0px 0px 4px gray;
$theme : rgba(200, 198, 198, 0.44);
$border : 10px;


.Categorire{
    margin: 3rem 0 2rem 0;
    width: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    .Categorire_card{
        width: 165px;
        height: 165px;
        box-shadow: $shadow;
        margin: 1rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border-radius: $border;
        &:hover{
            background-color: $theme;
        }
        .icon{
            font-size: 5rem;
            line-height: 1;
        }
        .name{
            font-size: 1.3rem;
            font-weight: 600;
        }
    }

}